import React, { useState, useEffect } from 'react';
import Loader from './images/loader.svg';

const App = () => {
  const [state, setState] = useState({
    isLoading: true,
    products: [],
    activeProduct: null,
  });

  useEffect(() => {
    fetch(
      'https://www.secureserver.net/api/v1/catalog/562883/products?currencyType=GBP&marketId=en-GB'
    )
      .then((res) => res.json())
      .then((res) => {
        setState({
          ...state,
          isLoading: false,
          products: res,
        });
      });
    // eslint-disable-next-line
  }, []);

  const setActiveProduct = (e) => {
    setState({
      ...state,
      activeProduct: state.products.filter(
        (product) => product.id === e.target.dataset.id
      )[0],
    });
  };

  const clearModal = (e) => {
    setState({
      ...state,
      activeProduct: null,
    });
  };

  const handleCart = (e) => {
    const product = state.products.filter(
      (product) => product.id === e.target.dataset.id
    )[0];
    const cartItem = {
      id: product.id,
      quantity: 1,
    };
    const data = {
      items: [cartItem],
    };
    console.log(data);
    fetch(
      'https://www.secureserver.net/api/v1/cart/562883?itc=slp_rstdstors&redirect=false',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      }
    )
      .then((response) => response.json())
      .then((data) => {
        if (data.nextStepUrl) window.location.href = data.nextStepUrl;
        console.log('Success:', data);
      })
      .catch((error) => {
        console.error('Error:', error);
      });
  };

  if (state.isLoading)
    return (
      <div className='loader'>
        <img src={Loader} alt='Loading...' />
      </div>
    );

  return (
    <div>
      <nav className='navbar navbar-dark bg-dark'>
        <div className='container'>
          <span className='navbar-brand mb-0 h1'>Yotta Hosts API</span>
        </div>
      </nav>
      <div className='container mt-3'>
        <div className='row'>
          {state.products.map((product) => (
            <div className='col-md-6 mb-3' key={product.id}>
              <div className='card'>
                <img src={product.image} className='card-img-top py-5' alt='' />
                <div className='card-body'>
                  <h5 className='card-title'>{product.title}</h5>
                  <h6 className='card-subtitle mb-2'>
                    <span className='text-primary'>{product.listPrice}</span>{' '}
                    per {product.term} {` `}
                    {product.oneTimeCharge ? '(One Time Charge)' : ''}
                  </h6>
                  <small className='text-muted'>
                    Price excludes applicable taxes and{' '}
                    {product.displayTaxesAndFees} fees.
                  </small>{' '}
                  <br />
                  {!product.tld && (
                    <button
                      onClick={handleCart}
                      data-id={product.id}
                      className='btn btn-secondary mt-2'
                    >
                      Add to cart
                    </button>
                  )}
                  <div className='mt-2'>
                    Tags:{' '}
                    {product.tags.map((tag) => (
                      <div
                        key={tag}
                        className='badge badge-pill badge-info mr-1'
                      >
                        {tag.toLowerCase()}
                      </div>
                    ))}
                  </div>
                  <div className='mt-2'>
                    Categories:{' '}
                    {product.categories.map((category) => {
                      if (typeof category === 'object' && category !== null) {
                        return (
                          <div
                            key={category}
                            className='badge badge-pill badge-info mr-1'
                          >
                            {JSON.stringify(category)}
                          </div>
                        );
                      } else {
                        return (
                          <div
                            key={category}
                            className='badge badge-pill badge-info mr-1'
                          >
                            {category}
                          </div>
                        );
                      }
                    })}
                  </div>
                  <div
                    className='card-text mt-3'
                    dangerouslySetInnerHTML={{
                      __html: product.content,
                    }}
                  />
                  <button
                    data-id={product.id}
                    className='btn btn-link text primary'
                    onClick={setActiveProduct}
                  >
                    Read more
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
      {state.activeProduct && (
        <div
          className='modal fade show'
          id='exampleModal'
          tabIndex='-1'
          role='dialog'
          aria-labelledby='exampleModalLabel'
          aria-hidden='false'
          style={{ display: 'block', background: 'rgba(0,0,0,0.5)' }}
        >
          <div className='modal-dialog modal-dialog-scrollable modal-lg'>
            <div className='modal-content'>
              <div className='modal-header'>
                <h5 className='modal-title' id='exampleModalLabel'>
                  {state.activeProduct.title}
                </h5>
                <button
                  type='button'
                  className='close'
                  aria-label='Close'
                  onClick={clearModal}
                >
                  <span aria-hidden='true'>&times;</span>
                </button>
              </div>
              <div className='modal-body'>
                <div className='card'>
                  <img
                    src={state.activeProduct.image}
                    className='card-img-top py-5'
                    alt=''
                  />
                  <div className='card-body'>
                    <h5 className='card-title'>{state.activeProduct.title}</h5>
                    <h6 className='card-subtitle mb-2'>
                      <span className='text-primary'>
                        {state.activeProduct.listPrice}
                      </span>{' '}
                      per {state.activeProduct.term} {` `}
                      {state.activeProduct.oneTimeCharge
                        ? '(One Time Charge)'
                        : ''}
                    </h6>
                    <small className='text-muted'>
                      Price excludes applicable taxes and{' '}
                      {state.activeProduct.displayTaxesAndFees} fees.
                    </small>{' '}
                    <br />
                    {!state.activeProduct.tld && (
                      <button
                        onClick={handleCart}
                        data-id={state.activeProduct.id}
                        className='btn btn-secondary mt-2'
                      >
                        Add to cart
                      </button>
                    )}
                    <div className='mt-2'>
                      Tags:{' '}
                      {state.activeProduct.tags.map((tag) => (
                        <div
                          key={tag}
                          className='badge badge-pill badge-info mr-1'
                        >
                          {tag.toLowerCase()}
                        </div>
                      ))}
                    </div>
                    <div className='mt-2'>
                      Categories:{' '}
                      {state.activeProduct.categories.map((category) => {
                        if (typeof category === 'object' && category !== null) {
                          return (
                            <div
                              key={category}
                              className='badge badge-pill badge-info mr-1'
                            >
                              {JSON.stringify(category)}
                            </div>
                          );
                        } else {
                          return (
                            <div
                              key={category}
                              className='badge badge-pill badge-info mr-1'
                            >
                              {category}
                            </div>
                          );
                        }
                      })}
                    </div>
                    <div
                      className='card-text mt-3'
                      dangerouslySetInnerHTML={{
                        __html: state.activeProduct.content,
                      }}
                    />
                  </div>
                </div>
              </div>
              <div className='modal-footer'>
                <button
                  type='button'
                  className='btn btn-secondary'
                  onClick={clearModal}
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default App;
